.movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

  padding-left: 0;

  &__item {
    width: 200px;
    margin: 5px;
    // border: 1px solid #9098a3 ;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    // margin-bottom: 10px;

    // &:not(:nth-last-child(-n + 2)) {
    //   margin-bottom: 10px;
    // }
  }

  &__link {
    text-decoration: none;
    color: #022147;
    font-weight: 400px;
    //  padding: 10px;
  }

  &__title {
    display: block;
    text-align: center;
    padding: 20px;
  }
}
